import React, { Component } from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faFile, faEnvelopeOpen } from '@fortawesome/free-regular-svg-icons'
import { faGithubAlt, faLinkedinIn } from '@fortawesome/free-brands-svg-icons'

class Footer extends Component {
  constructor(props) {
    super(props)
      this.state = {
        icon1: true,
        icon2: true,
        icon3: true,
        icon4: true,
      }

    this.handleMouseOver = this.handleMouseOver.bind(this)
    this.handleMouseLeave = this.handleMouseLeave.bind(this)
  }

  componentDidMount() {
    this.setState({icon1: true, icon2: true, icon3: true, icon4: true})
  }

  handleMouseOver(event) {
    this.setState({[event.currentTarget.id]: false})
    console.log(this.state.icon1)
  }

  handleMouseLeave(event) {
    this.setState({[event.currentTarget.id]: true})
    console.log(this.state.icon1)
  }

  handleClick() {
    window.scroll({top: 0, behavior: 'smooth'})
  }


  render() {
    const {icon1, icon2, icon3, icon4} = this.state
    return (
        <div className="footer">
          <div className="footer-circle">
            <div className="inner-circle">
            </div>
          </div>
          <div className="footer-container">
            <div className="icon-wrapper">
              <a href="$"  target="_blank" rel="noopener noreferrer">
              <div id="icon1" onMouseEnter={this.handleMouseOver}
                              onMouseLeave={this.handleMouseLeave}
                              className="icon-hover">
                {icon1 ?
                <FontAwesomeIcon icon={faFile} className="footer-icon"/> :
                <p>resume</p>}
              </div></a>
              <a href="https://github.com/mmarsden89"  target="_blank" rel="noopener noreferrer">
              <div id="icon2" onMouseEnter={this.handleMouseOver}
                              onMouseLeave={this.handleMouseLeave}
                              className="icon-hover">
              {icon2 ?
                <FontAwesomeIcon icon={faGithubAlt} className="footer-icon"/> :
                <p>github</p>}
              </div>
              </a>
              <p className="back-to-top" onClick={this.handleClick}>back to top</p>
              <a href="https://www.linkedin.com/in/matthewjmarsden/" target="_blank" rel="noopener noreferrer">
              <div id="icon3" onMouseEnter={this.handleMouseOver}
                              onMouseLeave={this.handleMouseLeave}
                              className="icon-hover">
              {icon3 ?
                <FontAwesomeIcon icon={faLinkedinIn} className="footer-icon"/> :
                <p>linkedin</p>}
              </div>
              </a>
              <a href="mailto:mmarsden89@gmail.com" target="_blank" rel="noopener noreferrer">
              <div id="icon4" onMouseEnter={this.handleMouseOver}
                              onMouseLeave={this.handleMouseLeave}
                              className="icon-hover">
              {icon4 ?
                <FontAwesomeIcon icon={faEnvelopeOpen} className="footer-icon"/> :
                <p>email</p>}
              </div>
              </a>
            </div>
            <div className="info-wrapper">
              <p>© Matt Marsden 2019</p>
            </div>
          </div>
        </div>
    )
  }
}

export default Footer
