import React, { Component } from 'react'

class Navbar extends Component {
  constructor(props) {
    super(props)
    this.state = {
      name1: "",
      name2: "",
      name3: "",
      name4: "",
      name5: "",
      name6: "",
      translate1: 0
    }
    this.handleScroll = this.handleScroll.bind(this)
  }

  componentDidMount() {
    window.addEventListener('scroll', this.handleScroll, { passive: true })
    this.setState({ name1: <pre>   </pre>})
    this.setState({ name2: "matt"})
    this.setState({ name3: <pre> mars</pre>})
    this.setState({ name4: "den"})
    this.setState({ name5: ""})
    this.setState({ name6: "."})
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this.handleScroll)
  }

  handleScroll(event) {
    let scrollTop = Math.round(window.scrollY),
        itemTranslate = Math.round(scrollTop / 3)
        console.log(itemTranslate)
        if (itemTranslate < 5) {
          this.setState({ name1: <pre>   </pre>})
          this.setState({ name2: "matt"})
          this.setState({ name3: <pre> mars</pre>})
          this.setState({ name4: "den"})
          this.setState({ name6: "."})
          this.setState({ translate1: 0})
        } else if (itemTranslate < 40) {
          this.setState({ name1: "<" })
          this.setState({ name2: "m" })
          this.setState({ name3: <pre>att </pre> })
          this.setState({ name4: "marsden" })
          this.setState({ name5: "89/" })
          this.setState({ name6: ">" })
          this.setState({ translate1: 0 })
        } else if (itemTranslate < 180 ) {
          this.setState({ name1: "<" })
          this.setState({ name2: "m" })
          this.setState({ name3: <pre>att </pre> })
          this.setState({ name4: "marsden" })
          this.setState({ name5: "89/" })
          this.setState({ name6: ">" })
          this.setState({ translate1: itemTranslate / 5 })
        } else if (itemTranslate > 181) {
          this.setState({ name1: "<" })
          this.setState({ name2: "m" })
          this.setState({ name3: <pre>att </pre> })
          this.setState({ name4: "marsden" })
          this.setState({ name5: "89/" })
          this.setState({ name6: ">" })
          this.setState({ translate1: 181 / 5})
        }
  }

  render() {
    return (
      <nav className="navbar">
        <div className="nav-container">
          <div className="nav-name">
            <h1 onScroll={this.handleScroll}
                className="nav-text ">{this.state.name1}</h1>
            <h1 onScroll={this.handleScroll}
                className="nav-text open">{this.state.name2}</h1>
            <h1 onScroll={this.handleScroll}
                style={{marginLeft: `-${this.state.translate1}px`}}>{this.state.name3}</h1>
            <h1 onScroll={this.handleScroll}
                style={{ backgroundColor: '#282c34'}}>{this.state.name4}</h1>
            <h1 onScroll={this.handleScroll}
                className="numbers">{this.state.name5}</h1>
            <h1 onScroll={this.handleScroll}
                className="nav-text close">{this.state.name6}</h1>
          </div>
          <div className="nav-link-container">
            <p className="nav-link">resume</p>
            <a href={'#portfolio'} className="nav-link">work</a>
          </div>
        </div>
      </nav>
    )
  }
}

export default Navbar
