import React, { Component } from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faCameraRetro, faSearch } from '@fortawesome/free-solid-svg-icons'

class Project extends Component {
  constructor(props) {
    super(props)
    this.state = {
      props: this.props
    }
  }

  render() {
    const props = this.props.props
    return (
      <div className="project-container">
        {props.image !== "null" ?
        <div className="project-wrapper">
          <a href={props.link} target="#">
            <img src={props.hoverImage} className={`project-pic project-pic-hover ${props.class}3`} alt=""/>
            <h1 className={`project-header ${props.class}2`}
                style={{'fontFamily' : props.font, color: props.fontColor}}>{props.title}</h1>
            <img src={props.image} className={`${props.class} project-pic`} alt=""/>
          </a>
        </div>
         :
         <div className="project-wrapper">
           <a href={props.link} target="#">
             <img src={props.hoverImage} className={`project-pic project-pic-hover ${props.class}3`} alt=""/>
             <h1 className={`project-header ${props.class}2`}
                 style={{'fontFamily': props.font, color: props.fontColor}}>{props.title}</h1>
             {props.title === 'quackle' ?
             <div>
               <FontAwesomeIcon icon={faSearch} className="search-icon"/>
                <div className={`${props.class} project-pic`}>
                 <FontAwesomeIcon icon={faCameraRetro} style={{color: props.fontColor}}/>
               </div>
             </div> :
             <div className="project-pic">
           </div>}
           </a>
         </div>
       }
        <div className="project-desc-cont">
          <p className="project-desc">{props.desc}</p>
          <div className="links">
            <a href={props.link1} target="_blank" rel="noopener noreferrer" alt="" className="project-links">deployed application</a>
            <a href={props.link2} target="_blank" rel="noopener noreferrer" alt="" className="project-links">front-end repo</a>
            <a href={props.link3} target="_blank" rel="noopener noreferrer" alt="" className="project-links">back-end repo</a>
          </div>
          <div className="tech-container">
            <div title={props.tech1[1]}>
              {props.tech1[0] === "img/express.svg" || props.tech1[0] === "img/mongodb.svg" ?
                <img src={props.tech1[0]} className="svg backed" alt=""/> :
                <img src={props.tech1[0]}  className="svg" alt=""/>}
            </div>
            <div title={props.tech2[1]}>
              {props.tech2[0] === "img/express.svg" || props.tech2[0] === "img/mongodb.svg" ?
                <img src={props.tech2[0]} className="svg backed" alt=""/> :
                <img src={props.tech2[0]}  className="svg" alt=""/>}
            </div>
            <div title={props.tech3[1]}>
            {props.tech3[0] === "img/express.svg" || props.tech3[0] === "img/mongodb.svg" ?
              <img src={props.tech3[0]} className="svg backed" alt=""/> :
              <img src={props.tech3[0]}  className="svg" alt=""/>}
            </div>
            <div title={props.tech4[1]}>
            {props.tech4[0] === "img/express.svg" || props.tech4[0] === "img/mongodb.svg" ?
              <img src={props.tech4[0]} className="svg backed" alt=""/> :
              <img src={props.tech4[0]}  className="svg" alt=""/>}
            </div>
            {props.tech5[1] ? <div title={props.tech5[1]}>
            {props.tech5[0] === "img/express.svg" || props.tech5[0] === "img/mongodb.svg" ?
              <img src={props.tech5[0]} className="svg backed" alt=""/> :
              <img src={props.tech5[0]}  className="svg" alt=""/>}
            </div> : null}
            {props.tech6[1] ? <div title={props.tech6[1]}>
            {props.tech6[0] === "img/express.svg" || props.tech6[0] === "img/mongodb.svg" ?
              <img src={props.tech6[0]} className="svg backed" alt=""/> :
              <img src={props.tech6[0]}  className="svg" alt=""/>}
            </div> : null}
            {props.tech7[1] ? <div title={props.tech7[1]}>
            {props.tech7[0] === "img/express.svg" || props.tech7[0] === "img/mongodb.svg" ?
              <img src={props.tech7[0]} className="svg backed" alt=""/> :
              <img src={props.tech7[0]}  className="svg" alt=""/>}
            </div> : null}
          </div>
        </div>
      </div>
    )
  }
}

export default Project
